
import Vue from 'vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import Search from 'vux/src/components/search/index.vue'
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'
import Divider from 'vux/src/components/divider/index.vue'
import AlertPlugin from 'vux/src/plugins/alert/index.js'
import ConfirmPlugin from 'vux/src/plugins/confirm/index.js'

import Loading from './pages/components/Loading'
import FullscreenImg from './pages/components/FullscreenImg'

// 全局注册vux的组件
Vue.component('Group', Group)
Vue.component('Cell', Cell)
Vue.component('Datetime', Datetime)
Vue.component('ButtonTab', ButtonTab)
Vue.component('ButtonTabItem', ButtonTabItem)
Vue.component('Divider', Divider)
Vue.component('Search', Search)
Vue.component('XInput', XInput)
Vue.component('XButton', XButton)
Vue.component('XHeader', XHeader)
Vue.component('XSwitch', XSwitch)

Vue.use(AlertPlugin)
Vue.use(ConfirmPlugin)

import NavBar from './pages/components/NavBar'
import cookie from './utils/cookie'
import pageUtil from './utils/page'

const sessionHistory = window.sessionStorage

export default {
data () {
return {
transitionName: 'forward'
}
},
watch: {
// 更新页面所在位置，用于判断是前进页还是后退页
'$route' (to, from) {
if (to && from) {
let toPath = to.path
let fromPath = from.path
let count = parseInt(sessionHistory.getItem('count'))
// 如果是导航页或者没有初始记录
if (Number.isNaN(count)) {
count = 1
this.transitionName = 'forward'
} else {
count += 1
let fromCount = parseInt(sessionHistory.getItem(fromPath))
let toCount = parseInt(sessionHistory.getItem(toPath))
if (toCount < fromCount && fromCount < count && (!pageUtil.showNav(fromPath))) {
this.transitionName = 'backward'
count = toCount
} else {
this.transitionName = 'forward'
}
if (pageUtil.showNav(toPath)) {
count = 1
}
}
sessionHistory.setItem(toPath, count)
sessionHistory.setItem('count', count)
}
}
},
// 所有页面更新都会触发此函数
updated () {
// 提交sdk连接请求
this.$store.dispatch('connect')
this.$store.dispatch('updateRefreshState')
},
components: {
NavBar,
ViewBox,
Loading,
FullscreenImg
},
computed: {
// 是否显示导航条
showNav () {
return pageUtil.showNav(this.$route.path)
}
}
}



